'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import supabase from '@/lib/supabaseBrowserClient'
import Link from 'next/link'

export default function ProfilePage() {
  const [user, setUser] = useState<any>(null)
  const router = useRouter()

  useEffect(() => { 
    const getUser = async () => {
      const { data, error } = await supabase.auth.getUser() // asynchronous call to get the current user
      if (data?.user) { // check if user data is available
        setUser(data.user)
      } else {
        console.error('No user found or error:', error)
      }
    }

    getUser()
  }, [])

  return (
    <div className="p-6 max-w-xl mx-auto">
      {user ? (
        <>
          <h1 className="text-xl font-semibold mb-2">Welcome!</h1>
          <p className="text-gray-800">Logged in as: {user.email}</p>
          <Link
        href="/post"
        className="inline-block bg-black text-white px-4 py-2 rounded hover:bg-gray-800"
      >
        + Post a New Request
      </Link>
        </>
      ) : (
        <p>Loading user info...</p>
        
      )}
    </div>
  )
}
